import React from 'react';
import styles from './MainSettings.module.css';


const MainSettings = () => {
    return (<div className={styles.mainContent}>

        <div className={styles.parameter}>
            <span className={styles.parameterName}>   </span>
            <textarea> </textarea>
        </div>      
       

        <div className={styles.parameter}>
            <span className={styles.parameterName}>    </span>
          <textarea> </textarea>
        </div>      
       
        <div className={styles.parameter}>
            <span className={styles.parameterName}>   <div className={styles.info}>(   )</div></span>
            <input type="date" />
        </div>      
       
        <div className={styles.parameter}>
            <button type='submit' className={styles.parameterSave}>  </button>
        </div>
       
    </div>);
}


export default MainSettings;